.explore-men{
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.explore-men h1{
  color: #262626;
  font-weight: 500;
}
.explore-menu-text p {
  max-width: 60%;
  columns: #808080;
}
.explore-menu-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  text-align:center ;
  margin: 20px 0px;
  overflow-x: scroll;
}
.explore-menu-list::-webkit-scrollbar{
  display: none;
}
.explore-menu-list-item img{
width: 7.5vw;
min-width: 80px;
cursor: pointer;
border-radius: 50%;
transition: 0.2s;
}
.explore-menu-list-item p{
  margin-top: 10px;
  color: #747474;
  font-size:max(1.4vw,20px ) ;
  cursor: pointer;
}
.explore-men hr{
  margin: 10px 0px;
  background-color: #e2e2e2;
  bottom: none;
}
.explore-menu-list-item .active{
  border: 4px solid tomato;
  padding: 2px;
}
@media(max-width:400px){
  .explore-menu-text{
    max-width: 100%;
    font-size: 14px;
  }
}