.header {
  height: 34vw;
  margin: 30px auto;
  background: url('../../Assets/header_img.png') no-repeat center center;
  background-size: cover; /* Changed to cover for better responsiveness */
  position: relative;
}

/* Content inside the header */
.header-contents {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Changed to flex-start for better alignment */
  gap: 1.5vw;
  max-width: 50%;
  left: 6vw;
  animation: fadeIn 3s;
  bottom: 10%;
}

.header-contents h2 {
  font-weight: 500;
  color: white;
  font-size: max(4.5vw, 22px);
}

.header-contents p {
  color: white;
  font-size: 1vw;
}

.header-contents button {
  border: none;
  color: #747474;
  font-weight: 500;
  padding: 1vw 2.3vw;
  background-color: white;
  font-size: max(1vw, 13px);
  border-radius: 50px;
}

/* Media Queries */

/* Tablets and medium screens */
@media (max-width: 1050px) {
  .header-contents {
    max-width: 45%;
  }
}

/* Small screens and mobile phones */
@media (max-width: 750px) {
  .header {
    height: 50vw; /* Increase height for better visibility on small screens */
  }

  .header-contents {
    max-width: 65%;
    left: 4vw; /* Slightly reduce left alignment */
  }

  .header-contents h2 {
    font-size: max(6vw, 18px); /* Increase font size for small screens */
  }

  .header-contents p {
    display: none; /* Hide paragraph text on small screens */
  }

  .header-contents button {
    padding: 2vw 4vw;
    font-size: max(2vw, 14px); /* Adjust font size for buttons */
  }
}

/* Extra small screens (phones <480px) */
@media (max-width: 480px) {
  .header {
    height: 60vw; /* Increase height for mobile view */
  }

  .header-contents {
    max-width: 75%; /* Make the content a bit larger */
    left: 3vw; /* Adjust alignment */
  }

  .header-contents h2 {
    font-size: max(8vw, 16px); /* Scale the text more */
  }

  .header-contents button {
    padding: 3vw 5vw; /* Adjust padding for buttons */
  }
}
