.login-popup{
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color:#00000090 ;
  display: grid;
}
.login-popup-container{
  place-self: center;
  width: max(23vw,330px);
  color: #808080;
  background-color: white;
  display: flex;
  flex-direction: column;
  gap: 25px;
  padding: 25px 30px;
  border-radius:8px ;
  font-size: 14px;
  animation: fadeIn 0.5s;
}
.login-popup-title{
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: tomato;
  font-weight: bold;
  font-size: x-large;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
.login-popup-title img{
  width: 15px;
  cursor: pointer;
}

.login-popup-input{
  display: flex;
  flex-direction: column;
  gap: 30px ;
 
}
.login-popup-input{
  outline: none;
  border: 1px solid whitesmoke;
  padding: 10px;
  border-radius: 4px;
  font-size: 20px;
}
.login-popup-container button{
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: white;
  background-color: tomato;
  font-size: 20px;
  cursor: pointer;
}
.login-popup-condition{
  display: flex;
  align-items: start;
  gap: 8px;
  margin-top: -15px;
}
.login-popup-condition input{
  margin-top: 5px;
}
.login-popup p span{
  color: tomato;
  font-weight: 500;
  cursor: pointer;
}