.cart{
margin-top: 100px;
}
.cart-item{

}
.cart-items-title{
display: grid;
grid-template-columns:1fr 1.5fr 1fr 1fr 1fr 0.5fr ;
color: grey;
font-size: max(1vw,12px);
}
.cart-items-item{
  margin: 10px 0px;
  color:black ;

}
.cart-items-item img{
  width: 50px;
}
.cart hr{
  height: 1px;
  border-color: #e2e2e2;
  bottom: none;
}
.cart-items-item .cross{
  cursor: pointer;
}
.cart-bottom{
  margin: 80px;
  display: flex;
  justify-content: space-between;
  gap: (12vw,20px);
}
.cart-total{
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.cart-total-details{
  display: flex;
  justify-content: space-between;
  color: #555;
}
.cart-total hr{
  margin: 10px 0px;
}
.cart-total button{
  border: none;
  color: white;
  background-color: tomato;
  width: max(15vw,200px);
  padding: 10px 0px;
  border-radius: 4px;
  cursor: pointer;
}
.cart-promocode{
  flex: 1;

}
.cart-promocode p{
  color: #555;

}
.cart-promocode-input{
  width: 300px;
  height: 6vh;
  margin-top: -15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #eaeaea;
  border-radius: 4px;
  margin-left: 3%;
}
.cart-promocode-input input{
  background: transparent;
  border: none;
  outline: none;
  padding-left: 10px;
}
.cart-promocode-input button{
  width: max(10vw,100px);
  padding: 10px 5px;
  background-color: black;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
.cart-promocode-input button:hover{
background-color: rgb(30, 67, 76);

}
@media (max-width:750px){
  .cart-bottom{
    flex-direction: column-reverse;
  }
  .cart-promocode{
    justify-content: start;
  }
}